import React, {useState} from 'react';
import HTMLReactParser from 'html-react-parser';
import { useParams} from 'react-router-dom';
import millify from 'millify';
import {Col, Row, Typography, Select} from 'antd';
import {MoneyCollectOutlined, DollarCircleOutlined, FundOutlined, ExclamationCircleOutlined, StopOutlined, NumberOutlined, PercentageOutlined, TrophyOutlined, CheckOutlined} from '@ant-design/icons';
import LineChart from './LineChart';
import Loader from './Loader';

import {useGetCryptoDetailsQuery, useGetCryptoHistoryQuery} from '../services/cryptoApi';

const {Title, Text} = Typography;
const {Option} = Select;

const CryptoDetails = () => {
    const {coinId} = useParams();
    const [timePeriod, setTimePeriod] = useState('7d')
    const {data, isFetching} = useGetCryptoDetailsQuery(coinId)
    const {data: coinHistory} = useGetCryptoHistoryQuery({coinId, timePeriod})
    const cryptoDetails = data?.data?.coin;

    if(isFetching) return <Loader/>;

    const time = ['3hr', '24h', '7d', '30d', '1y', '3m', '3y', '5y'];

    const stats = [
        {title: 'Price to USD', value: `$ ${cryptoDetails.price && millify(cryptoDetails.price)}`, icon: <DollarCircleOutlined/>},
        {title: 'Rank', value: cryptoDetails.rank, icon: <NumberOutlined/>},
        {title: 'Change', value: `${coinHistory?.data?.change}%`, icon: <PercentageOutlined />},
        {title: 'Market Cap', value: `$ ${cryptoDetails.marketCap && millify(cryptoDetails.marketCap)}`, icon: <DollarCircleOutlined/>},
        {title: 'All Time High', value: `$ ${millify(cryptoDetails.allTimeHigh.price)}`, icon: <TrophyOutlined/>},
    ];

    const genericStats = [
        {title:'Number Of Markets' , value: millify(cryptoDetails.numberOfMarkets), icon:<FundOutlined/>},
        {title:'Number Of Exchanges' , value: cryptoDetails.numberOfExchanges, icon:<MoneyCollectOutlined/>},
        {title:'Approved Supply' , value: cryptoDetails.supply.confirmed ? <CheckOutlined/> : <StopOutlined/>, icon: <ExclamationCircleOutlined />},
        {title:'Total Supply' , value: `$ ${millify(cryptoDetails.supply.total)}`, icon:<ExclamationCircleOutlined/>},
        {title:'Circulating Supply' , value: `$ ${millify(cryptoDetails.supply.circulating)}` , icon:<ExclamationCircleOutlined/>},
    ] 

    console.log(data)
    return (
        <Col className='coin-detail-container'>
            <Col className='coin-heading-container'>
                <Title level={1} className='coin-name'>
                    {cryptoDetails.name} ({cryptoDetails.symbol}) Price
                </Title>
                <p>
                    {cryptoDetails.name} live price in US dollars.
                    View value statistics, market cap and supply.
                </p>
            </Col>
            <Select defaultValue="7d" className='select-timeperiod' placeholder='Select Time Period' onChange={(value)=> setTimePeriod(value)}>
                {
                    time.map((date)=> <Option key={date}>{date}</Option>)
                }
            </Select>
            <LineChart coinHistory={coinHistory} currentPrice={millify(cryptoDetails.price)} coinName={cryptoDetails.name}/>
            {/* Selected Coin Value Statistics Column */}
            <Col className='stats-container'>
                <Col className='coin-value-statistics'>
                    <Col className='coin-value-statistics-heading'>
                        <Title level={2} className='coin-details-heading'>
                            {cryptoDetails.name} Value Statistics
                        </Title>
                        <p>
                            An overview showing the stats of {cryptoDetails.name}
                        </p>
                    </Col>
                    {
                        stats.map(({icon, title, value, i})=>(
                            <Col className='coin-stats' key={i}>
                                <Col className='coin-stats-name'>
                                    <Text>{icon}</Text>
                                    <Text>{title}</Text>
                                </Col>
                                <Text className='stats'>{value}</Text>
                            </Col>
                        ))
                    }
                </Col>
            {/* All Coins Statistics Column */}
                <Col className='other-stats-info'>
                    <Col className='coin-value-statistics-heading'>
                        <Title level={2} className='coin-details-heading'>
                            Other Statistics
                        </Title>
                        <p>
                            An overview showing the stats of all cryptocurrencies
                        </p>
                    </Col>
                    {
                        genericStats.map(({icon, title, value, i})=>(
                            <Col className='coin-stats' key={i}>
                                <Col className='coin-stats-name'>
                                    <Text>{icon}</Text>
                                    <Text>{title}</Text>
                                </Col>
                                <Text className='stats'>{value}</Text>
                            </Col>
                        ))
                    }
                </Col>
            </Col>
            {/* Description and Additional Links */}
            <Col className='coin-desc-link'>
                <Row className='coin-desc'>
                    <Title level={3} className='coin-details-heading'>
                        What is {cryptoDetails.name}?
                        {HTMLReactParser(cryptoDetails.description)}
                    </Title>
                </Row>
                <Col className='coin-links'>
                    <Title level={3} className='coin-details-heading'>
                        {cryptoDetails.name} Links
                    </Title>
                    {cryptoDetails.links.map((link, i)=>(
                        <Row className='coin-link' key={i}>
                            <Title level={5} className='link-name'>
                                {link.type}
                            </Title>
                            <a href={link.url} target="_blank" rel="noreferrer">{link.name}</a>
                        </Row>
                    ))}
                
                </Col>
            </Col>
        </Col>
    )
}

export default CryptoDetails;